/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Col } from 'reactstrap';

import PromoCode from 'components/FormsV3/PromoCode/PromoCode';

import './CheckoutPriceSummary.scss';

class CheckoutPriceSummary extends Component {
  renderPromoCodeButton() {
    const {
      getPromo,
      isAdHoc,
      charged,
      promoCode,
      handleOnChange,
    } = this.props;

    if (charged || isAdHoc) {
      return (
        <Fragment>
          <Col xs={12} sm={7} />
          <Col xs={4} sm={7} className="d-block d-sm-none" />
        </Fragment>
      );
    }

    // if (isAdHoc) {
    //   return <div className="promo" />;
    // }

    return (
      <Fragment>
        <Col xs={12} sm={7} className="promo">
          <PromoCode
            value={promoCode}
            name="promo_code"
            placeholderString="Promo Code"
            postCheckout={async (promo) => {
              await handleOnChange('text', 'promo_code', promo);
              const promoResult = await getPromo();
              const actualPromo = promoResult && promoResult.promo && promoResult.promo.code;
              if (actualPromo !== promo) {
                await handleOnChange('text', 'promo_code', null);
                return false;
              }

              return true;
            }}
          />
        </Col>
        <Col xs={4} sm={7} className="d-block d-sm-none" />
      </Fragment>
    );
  }

  render() {
    const {
      transactionData,
      charged,
      isRenderPromo,
      isAdHoc,
    } = this.props;

    if (
      !transactionData ||
      !transactionData.transaction ||
      !transactionData.transaction.price
    ) {
      return null;
    }

    const priceData = transactionData.transaction.price;
    const JSX = _.reduce(priceData, (result, price, index) => {
      if (index === priceData.length - 1 || price.title === 'P$ Cashback') {
        return result;
      }
      const noTitle = !price.title || price.title === '';
      const isPromo = price.title !== 'Subtotal' &&
                      price.title !== 'Discount' &&
                      !noTitle;

      if ((isPromo && !isRenderPromo) || (!isPromo && isRenderPromo)) {
        return result;
      }

      const extraCol = isPromo ? null : <Col xs={4} sm={7} />;
      const promoStyle = isPromo ? 'promo-item' : '';
      const subTotalExtra = price.title === 'Subtotal' ? 'GST 7% included' : '';

      const element = (
        <Fragment key={`price-data-${index}`}>
          {extraCol}
          <Col className={`summary-item-container ${promoStyle}`} xs={8} sm={5}>
            {
              noTitle ?
                <span className="summary-item-desc">{price.sub_title}</span>
              :
                <div className="summary-left">
                  <span className="summary-item-desc">{price.title} {price.sub_title}</span>
                  <span className="summary-item-sub-desc">{subTotalExtra}</span>
                </div>
            }
            <span className="summary-item-value">{price.description}</span>
          </Col>
        </Fragment>
      );

      result.push(element);
      return result;
    }, []);

    if (!isRenderPromo) {
      return JSX;
    }

    if ((charged || isAdHoc) && _.isEmpty(JSX)) {
      return null;
    }

    return (
      <Fragment>
        <Col xs={12} className="summary-seperator" />
        {this.renderPromoCodeButton()}
        {JSX}
      </Fragment>
    );
  }
}

CheckoutPriceSummary.defaultProps = {
  isAdHoc: false,
  charged: false,
  promoCode: null,
};

CheckoutPriceSummary.propTypes = {
  isAdHoc: PropTypes.bool,
  isRenderPromo: PropTypes.bool.isRequired,
  charged: PropTypes.bool,
  transactionData: PropTypes.object.isRequired,
  getPromo: PropTypes.func.isRequired,
  promoCode: PropTypes.string,
  handleOnChange: PropTypes.func.isRequired,
};

export default CheckoutPriceSummary;
