/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import TabView from 'components/Templates/TabView/TabViewNew';

import './CheckoutDetailsTable.scss';

class CheckoutDetailsTable extends Component {
  generateTabs() {
    const { subDetails } = this.props;
    return _.reduce(subDetails, (result, data) => {
      result.push({
        desc: data.key,
      });

      return result;
    }, []);
  }

  renderDetails(details, isFullWidth, isMain = false) {
    const { transactionData } = this.props;
    if (!details) {
      return null;
    }

    let smLeftWidth = 5;
    let smRightWidth = 7;
    smLeftWidth = isFullWidth ? 12 : smLeftWidth;
    smRightWidth = isFullWidth ? 12 : smRightWidth;

    // let paddingStyle = isLeftSide ? 'detail-padding-right' : 'detail-padding-left';
    // paddingStyle = isFullWidth ? '' : paddingStyle;

    let coverageData = {};

    if (isMain) {
      try {
        coverageData = {
          title: 'Coverage',
          description: _.find(transactionData.transaction.policy_info, data => data.title.toUpperCase() === 'COVERAGE').description,
        };
      } catch (error) {
        // No coverage found
      }
    }

    const attributes = [...details.attributes, coverageData];
    const infoList = _.map(attributes, (info, index) => (
      <Col
        xs={12}
        className="detail-group"
        key={`${info.title}-${index}`}
      >
        <Row noGutters>
          <Col className="detail-desc" xs={12} sm={smLeftWidth}>{info.title}</Col>
          <Col className="detail-value" xs={12} sm={smRightWidth}>{info.description}</Col>
        </Row>
      </Col>
    ));

    return (
      <Col className="detail-column" xs={12} sm={6}>
        <Row noGutters>
          <Col className="detail-header" xs={12}>{details.title}</Col>
          {infoList}
        </Row>
      </Col>
    );
  }

  renderMainDetails() {
    const { mainDetails } = this.props;
    if (_.isEmpty(mainDetails.attributes)) {
      return null;
    }
    return this.renderDetails(mainDetails, false, true);
  }

  renderSubDetails() {
    const { subDetails, charged } = this.props;

    if (_.isArray(subDetails)) {
      if (subDetails.length === 1) {
        return (
          <Row className="checkout-detail-container" noGutters>
            {this.renderDetails(subDetails[0].leftDetail, false)}
            {this.renderDetails(subDetails[0].rightDetail, false)}
          </Row>
        );
      }

      if (subDetails.length > 1) {
        return (
          <TabView
            style={{ marginTop: 44, marginLeft: 30, marginRight: 30 }}
            tabs={this.generateTabs()}
            maxTabWidthPx={128}
            autoHeight
            name="travel-checkout-tab"
            charged={charged}
            scrollTabs
          >
            {
              _.map(subDetails, data => (
                <Fragment key={data.key}>
                  <Row className="checkout-detail-container" noGutters>
                    {this.renderDetails(data.leftDetail, false)}
                    {this.renderDetails(data.rightDetail, false)}
                  </Row>
                </Fragment>
              ))
            }
          </TabView>
        );
      }
    }

    return null;
  }

  render() {
    const innerJSX = (
      <Col className="no-padding"xs={12}>
        <Row className="checkout-detail-container main-details" noGutters>
          {this.renderMainDetails()}
        </Row>
        {this.renderSubDetails()}
      </Col>
    );

    return innerJSX;
  }
}

CheckoutDetailsTable.defaultProps = {
  charged: false,
};

CheckoutDetailsTable.propTypes = {
  subDetails: PropTypes.array.isRequired,
  mainDetails: PropTypes.object.isRequired,
  charged: PropTypes.bool,
  transactionData: PropTypes.object.isRequired,
};

export default CheckoutDetailsTable;
