/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Col } from 'reactstrap';

import './CheckoutTotal.scss';

class CheckoutTotal extends Component {
  render() {
    const { transactionData } = this.props;

    if (
      !transactionData ||
      !transactionData.transaction ||
      !transactionData.transaction.price
    ) {
      return null;
    }

    const priceData = transactionData.transaction.price;
    const totalData = _.find(priceData, price => price.title === 'Total');

    if (!totalData) {
      return null;
    }

    return (
      <Fragment>
        <Col xs={4} sm={7} />
        <Col className="summary-item-container" xs={8} sm={5}>
          <span className="summary-item-desc">{totalData.title}</span>
          <span className="summary-item-value final-amount">{totalData.description}</span>
        </Col>
      </Fragment>
    );
  }
}

CheckoutTotal.defaultProps = {
};

CheckoutTotal.propTypes = {
  transactionData: PropTypes.object.isRequired,
};

export default CheckoutTotal;
