/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import $ from 'jquery';
import document from 'global/document';
import window from 'global/window';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { Row, Col } from 'reactstrap';

import Button from 'components/FormsV3/Button/Button';

import PiggyFillForm from 'components/Templates/PiggySignUp/PiggyFillFormNew';
import HeaderDescWithTypeDate from 'components/Templates/InsurerFormHeader/HeaderDescWithTypeDateNew';

import { getFormValue } from 'lib/helperFunctions';
import {
  getAnonymousSession,
  getPromoCode,
  getReceipt,
  getTransactionStripeSession,
  postCheckout,
} from '../../../actions/insuranceActions';

import CheckoutDetailsTable from './CheckoutDetailsTable/CheckoutDetailsTable';
import CheckoutPriceSummary from './CheckoutPriceSummary/CheckoutPriceSummary';
import CheckoutCashback from './CheckoutCashback/CheckoutCashback';
import CheckoutTotal from './CheckoutTotal/CheckoutTotal';
import CheckoutDisclaimer from './CheckoutDisclaimer/CheckoutDisclaimer';
import CheckoutPayment from './CheckoutPayment/CheckoutPayment';

import './Checkout.scss';

class Checkout extends Component {
  constructor(props) {
    super(props);

    const {
      charged,
      trackingTags,
    } = this.props;

    this.state = {
      transactionData: null,
      errorMsg: null,
      values: {},
      errors: {},
      sessionId: null,
    };

    if (charged && trackingTags && trackingTags.receipt) {
      trackingTags.receipt();
    }


    this.getPromo = this.getPromo.bind(this);
    this.setErrors = this.setErrors.bind(this);
    this.handleOnChange = this.handleOnChange.bind(this);
    this.renderHeader = this.renderHeader.bind(this);
    this.renderDetailsTable = this.renderDetailsTable.bind(this);
    this.renderPriceSummary = this.renderPriceSummary.bind(this);
    this.renderTotal = this.renderTotal.bind(this);
    this.renderCashBack = this.renderCashBack.bind(this);
    this.renderDisclaimer = this.renderDisclaimer.bind(this);
    this.renderBackToDetail = this.renderBackToDetail.bind(this);
    this.renderSignUp = this.renderSignUp.bind(this);
    this.renderPayment = this.renderPayment.bind(this);
  }

  async componentDidMount() {
    const { values } = this.state;

    const {
      isAdHoc,
      params,
      verifyParams,
      category,
      userDetails,
      getPromo,
      handleAPI,
      sessionId,
      charged,
    } = this.props;

    const newSessionId = isAdHoc ? sessionId : await getAnonymousSession(params, false, verifyParams)();
    const transactionData = await handleAPI(() => getPromo(newSessionId, userDetails && userDetails.token, getFormValue(values.promo_code), getFormValue(values.cashback)));

    this.setState({
      sessionId: newSessionId,
      transactionData: charged ? { transaction: transactionData } : transactionData,
    });

    window.scrollTo(0, 0);

    if ($('#intercom-container')) {
      $('#intercom-container').hide();
    }

    if (this.gaCheckOut && category === 'travel') {
      const range = document.createRange();
      this.gaCheckOut.appendChild(range.createContextualFragment(`
      <!-- Google Code for PPS2 - Travel - CheckoutPage_ProceedPayment Conversion Page
      In your html page, add the snippet and call
      goog_report_conversion when someone clicks on the
      chosen link or button. -->
      <script type="text/javascript">
        /* <![CDATA[ */
        goog_snippet_vars = function() {
          var w = window;
          w.google_conversion_id = 865122565;
          w.google_conversion_label = "rJn8CLL_5oQBEIXywpwD";
          w.google_remarketing_only = false;
        }
        // DO NOT CHANGE THE CODE BELOW.
        goog_report_conversion = function(url) {
          goog_snippet_vars();
          window.google_conversion_format = "3";
          var opt = new Object();
          opt.onload_callback = function() {
          if (typeof(url) != 'undefined') {
            window.location = url;
          }
        }
        var conv_handler = window['google_trackConversion'];
        if (typeof(conv_handler) == 'function') {
          conv_handler(opt);
        }
      }
      /* ]]> */
      </script>
      <script type="text/javascript"
        src="//www.googleadservices.com/pagead/conversion_async.js">
      </script>
      `));
    }
  }

  componentWillUnmount() {
    if ($('#intercom-container')) {
      $('#intercom-container').show();
    }

    if (window.goog_report_conversion) {
      delete window.goog_report_conversion;
    }
  }

  async getPromo() {
    const {
      values,
      sessionId,
    } = this.state;

    const {
      getPromo,
      userDetails,
      handleAPI,
      charged,
    } = this.props;

    const transactionData = await handleAPI(() => getPromo(sessionId, userDetails && userDetails.token, getFormValue(values.promo_code), getFormValue(values.cashback)));
    this.setState({
      transactionData: charged ? { transaction: transactionData } : transactionData,
    });
    return transactionData;
  }

  async setErrors(newErrors) {
    const { errors } = this.state;
    return new Promise((resolve) => {
      this.setState({
        errors: {
          ...errors,
          ...newErrors,
        },
      }, resolve);
    });
  }

  async handleOnChange(type, name, value) {
    const { values, errors } = this.state;
    let newError = {};
    if (value) {
      newError = { [name]: null };
    }

    return new Promise((resolve) => {
      this.setState({
        values: {
          ...values,
          [name]: { type, value },
        },
        errors: { ...errors, ...newError },
      }, resolve);
    });
  }

  renderHeader() {
    const { headerData } = this.props;
    return (
      <HeaderDescWithTypeDate
        logo={headerData.logo}
        logoName={headerData.logoName}
        insurerDesc={headerData.insurerDesc}
        data1={headerData.data1}
        data1Value={headerData.data1Value}
        data2={headerData.data2}
        data2Value={headerData.data2Value}
      />
    );
  }

  renderDetailsTable() {
    const { transactionData } = this.state;
    const { mainDetails, subDetails, charged } = this.props;

    return (
      <CheckoutDetailsTable
        mainDetails={mainDetails}
        subDetails={subDetails}
        charged={charged}
        transactionData={transactionData}
      />
    );
  }

  renderPriceSummary(isRenderPromo = false) {
    const {
      transactionData,
      values,
    } = this.state;

    const {
      promoErrorString,
      isAdHoc,
      charged,
    } = this.props;

    return (
      <CheckoutPriceSummary
        charged={charged}
        isAdHoc={isAdHoc}
        isRenderPromo={isRenderPromo}
        transactionData={transactionData}
        getPromo={this.getPromo}
        promoErrorString={promoErrorString}
        promoCode={getFormValue(values.promo_code)}
        handleOnChange={this.handleOnChange}
      />
    );
  }

  renderTotal() {
    const { transactionData } = this.state;

    return (
      <CheckoutTotal
        transactionData={transactionData}
      />
    );
  }

  renderCashBack() {
    const {
      transactionData,
      values,
      errors,
    } = this.state;

    const {
      isLoggedIn,
      userDetails,
      charged,
    } = this.props;

    return (
      <CheckoutCashback
        charged={charged}
        transactionData={transactionData}
        values={values}
        errors={errors}
        isLoggedIn={isLoggedIn}
        rewardPoints={userDetails && userDetails.reward_points}
        handleOnChange={this.handleOnChange}
        getPromo={this.getPromo}
      />
    );
  }

  renderDisclaimer() {
    const {
      values,
      errors,
      errorMsg,
    } = this.state;
    const { wordings, charged } = this.props;

    return (
      <CheckoutDisclaimer
        charged={charged}
        values={values}
        errors={errors}
        wordings={wordings}
        handleOnChange={this.handleOnChange}
        errorMsg={errorMsg}
      />
    );
  }

  renderBackToDetail() {
    const { backToDetails, isAdHoc } = this.props;
    if (!isAdHoc && backToDetails) {
      return (
        <Button
          className="checkout-button"
          variant="medium-grey"
          width={300}
          height={72}
          onClick={backToDetails}
        >
          <p>Back to Edit Details</p>
        </Button>
      );
    }

    return null;
  }

  renderChargedHeader() {
    const { charged } = this.props;

    if (charged) {
      return (
        <Col className="charged-header" xs={12}>
          <p>Receipt</p>
        </Col>
      );
    }

    return null;
  }

  renderSignUp() {
    const {
      isLoggedIn,
      category,
      login,
      charged,
    } = this.props;

    if (!charged && !isLoggedIn) {
      return (
        <div className="piggy">
          <PiggyFillForm
            category={category}
            onClick={() => login.popup.openPopup(login.loginContainer.setInitialIndex(1))}
          />
        </div>
      );
    }

    return null;
  }

  renderWarning() {
    const { errorMsg } = this.state;

    if (!errorMsg) {
      return null;
    }

    return (
      <Col xs={12}>
        <p className="disclaimer-warning">{errorMsg}</p>
      </Col>
    );
  }

  renderPayment() {
    const {
      transactionData,
      values,
      sessionId,
    } = this.state;

    const {
      wordings,
      trackingTags,
      handleAPI,
      category,
      setCharged,
      charged,
      userDetails,
    } = this.props;

    return (
      <CheckoutPayment
        getStripe={getTransactionStripeSession}
        sessionId={sessionId}
        checkout={postCheckout}
        wordings={wordings}
        trackingTags={trackingTags}
        setError={errorMsg => this.setState({ errorMsg })}
        charged={charged}
        transactionData={transactionData}
        values={values}
        promoCode={getFormValue(values.promo_code)}
        useCashback={getFormValue(values.cashback)}
        handleAPI={handleAPI}
        setCharged={setCharged}
        category={category}
        getPromo={this.getPromo}
        id={userDetails && userDetails.id}
        setErrors={this.setErrors}
      />
    );
  }

  render() {
    const { transactionData } = this.state;

    return (
      <Row className="detail-main-container-new checkout-container" noGutters>
        {
          transactionData &&
          <Fragment>
            <Col xs={12} lg={7}>
              {/* <div className="d-block d-lg-none">
                {this.renderSignUp()}
              </div> */}
              <Row className="detail-left-container" noGutters>
                {this.renderChargedHeader()}
                <Col className="company-product-container" xs={12}>
                  {this.renderHeader()}
                </Col>
                {this.renderDetailsTable()}
              </Row>
            </Col>
            <Col lg={5}>
              {this.renderSignUp()}
              <Row className="detail-right-container summary-container" noGutters>
                <Col xs={12} className="summary-seperator" />
                {this.renderPriceSummary(false)}
                {this.renderPriceSummary(true)}
                {this.renderCashBack()}
                <Col xs={12} className="summary-seperator" />
                {this.renderTotal()}
                {this.renderDisclaimer()}
                {this.renderWarning()}
                {this.renderPayment()}
              </Row>
            </Col>
          </Fragment>
        }
        <div ref={node => this.gaCheckOut = node} />
      </Row>
    );
  }
}

Checkout.defaultProps = {
  promoErrorString: null,
  wordings: [],
  backToDetails: null,
  isAdHoc: false,
  trackingTags: null,
  charged: false,
  userDetails: null,
};

Checkout.propTypes = {
  userDetails: PropTypes.object,
  getPromo: PropTypes.func.isRequired,
  subDetails: PropTypes.array.isRequired,
  headerData: PropTypes.object.isRequired,
  mainDetails: PropTypes.object.isRequired,
  promoErrorString: PropTypes.string,
  isLoggedIn: PropTypes.bool.isRequired,
  login: PropTypes.object.isRequired,
  wordings: PropTypes.array,
  backToDetails: PropTypes.func,
  isAdHoc: PropTypes.bool,
  trackingTags: PropTypes.object,
  charged: PropTypes.bool,
  category: PropTypes.string.isRequired,
  handleAPI: PropTypes.func.isRequired,
  setCharged: PropTypes.func.isRequired,
};

const mapStateToProps = (state, ownProps) => {
  const adHocCharged = ownProps.isAdHoc && ownProps.adHocData && ownProps.adHocData.status === 'paid' ? true : null;
  const isCharged = ownProps.charged || adHocCharged;
  return ({
    login: state.login,
    transactionData: state.insurance.transactionData,
    promoErrorString: state.insurance.promoErrorString,
    userDetails: state.userDetails && state.userDetails.data,
    adHocCharged: ownProps.isAdHoc && ownProps.adHocData && ownProps.adHocData.status === 'paid' ? true : null,
    isCharged,
  });
};

// const mapDispatchToProps = dispatch => ({
// });

const mergeProps = (propsFromState, propsFromDispatch, ownProps) => {
  const getPromo = propsFromState.isCharged ?
    sessionId => getReceipt(sessionId)() :
    (sessionId, userToken, promocode, useReward) => getPromoCode(userToken, sessionId, promocode, useReward)();

  return ({
    ...ownProps,
    ...propsFromState,
    // ...propsFromDispatch,
    getPromo,
  });
};

export default connect(mapStateToProps, null, mergeProps)(Checkout);
