/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import $ from 'jquery';
import document from 'global/document';
import window from 'global/window';
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Col } from 'reactstrap';

import CreditCards from 'assets/images/common/CreditCards.png';
import LabelImg from 'assets/images/forms/Label.svg';

import Button from 'components/FormsV3/Button/Button';

import StripePayment from 'components/Templates/StripePayment/StripePayment';
import CheckoutUsingCashback from 'components/Templates/CheckoutPopup/CheckoutUsingCashback';
import CheckoutComplete from 'components/Templates/CheckoutPopup/CheckoutComplete';

import { getFormValue } from 'lib/helperFunctions';
import { validateForm } from 'lib/validatorsV2';


import './CheckoutPayment.scss';

// const termsError = 'Please agree to all the terms to proceed.';
const transactionError = 'Transaction error. Please try again.';

class CheckoutPayment extends Component {
  constructor(props) {
    super(props);

    this.state = {
      markup: null,
    };

    this.handlePaymentCallback = this.handlePaymentCallback.bind(this);
    this.handleOnCheckout = this.handleOnCheckout.bind(this);
    this.handleOnCheckoutZero = this.handleOnCheckoutZero.bind(this);
    this.renderCashBackEarned = this.renderCashBackEarned.bind(this);
  }

  handleOnCheckoutZero() {
    const { setError } = this.props;
    try {
      $('#checkout_form').get(0).submit();
    } catch (error) {
      setError('Payment failed! Please try again!');
    }
  }

  async handlePaymentCallback(data) {
    const {
      checkout,
      sessionId,
      id,
      handleAPI,
      trackingTags,
      transactionData,
      category,
      setCharged,
      getPromo,
    } = this.props;

    const checkoutResult = await handleAPI(() => checkout(sessionId, data, id)());
    if (checkoutResult && checkoutResult.charged) {
      await setCharged(checkoutResult.charged);
      await getPromo();
      if (trackingTags && trackingTags.receipt) {
        trackingTags.receipt();
      }
      let user = 'User';
      try {
        const userData = transactionData.transaction.users[0].details;
        const familyName = _.find(userData, d => d.title === 'Family Name');
        const givenName = _.find(userData, d => d.title === 'Given Name');
        user = `${givenName ? `${givenName.description} ` : ' '}${familyName ? familyName.description : ''}`;
      } catch (error) {
        console.log('Name not found in checkout.');
      }

      setTimeout(() => this.checkoutComplete.openCheckoutComplete(user), 300);

      if (category === 'travel') {
        const range = document.createRange();
        this.gaReceipt.appendChild(range.createContextualFragment(`
        <!-- Google Code for PPS - Travel - Receipt page Conversion Page -->
        <script type="text/javascript">
        /* <![CDATA[ */
        var google_conversion_id = 865122565;
        var google_conversion_label = "3xCyCO-P54QBEIXywpwD";
        var google_remarketing_only = false;
        /* ]]> */
        </script>
        <script type="text/javascript" src="//www.googleadservices.com/pagead/conversion.js">
        </script>
        <noscript>
        <div style="display:inline;">
        <img height="1" width="1" style="border-style:none;" alt="" src="//www.googleadservices.com/pagead/conversion/865122565/?label=3xCyCO-P54QBEIXywpwD&amp;guid=ON&amp;script=0"/>
        </div>
        </noscript>
        `));
      }
    }
  }

  async handleOnCheckout() {
    const {
      useCashback,
      promoCode,
      values,
      getStripe,
      sessionId,
      wordings,
      trackingTags,
      setError,
      id,
      setErrors,
    } = this.props;

    let isTnCChecked = true;
    _.each(wordings, (wording, index) => {
      isTnCChecked = isTnCChecked && getFormValue(values[`tnc_${index}`]);
    });

    const validatedTnC = validateForm(values, null, _.map(wordings, (w, index) => ({
      type: 'checkbox',
      slug: `tnc_${index}`,
      is_required: true,
    })));

    if (!_.isEmpty(validatedTnC)) {
      setErrors(validatedTnC);
      return;
    }

    if (isTnCChecked) {
      this.setState({
        markup: null,
      }, async () => {
        const result = await getStripe(sessionId, promoCode, useCashback && id)();
        if (result) {
          this.setState({
            markup: result,
          }, () => {
            try {
              this.stripePayment.invokeClick(() => setError(transactionError), result, () => {
                if (trackingTags && trackingTags.payment) {
                  trackingTags.payment({
                    useCashback,
                    promoCode,
                  });
                }
                if (window.goog_report_conversion) {
                  window.goog_report_conversion();
                }
              });
            } catch (error) {
              // esline-disable-next-line
            }
          });
        }
      });
    }
  }

  renderCashBackEarned() {
    const { transactionData } = this.props;

    if (
      !transactionData ||
      !transactionData.transaction ||
      !transactionData.transaction.cashback
    ) {
      return null;
    }

    const { cashback } = transactionData.transaction;

    return (
      cashback ?
        <div className="cashback-earned-container">
          <img src={LabelImg} alt="Label.svg" />
          <span>Earn ${cashback.toFixed(2)} Cashback</span>
        </div>
        :
        null
    );
  }

  render() {
    const { markup } = this.state;
    const { charged } = this.props;

    return (
      <Fragment>
        {
          !charged &&
          <Fragment>
            <Col xs={12} className="mt-3" />
            <Col className="credit-card-container" xs={6}>
              <img src={CreditCards} alt="CreditCard.png" />
            </Col>
            <Col className="checkout-button-container" xs={6}>
              <Button
                onClick={this.handleOnCheckout}
                className="checkout-button"
                type="submit"
                variant="medium-flat-green"
                height={48}
              >
                <p>Proceed to Payment</p>
              </Button>
              {this.renderCashBackEarned()}
            </Col>
          </Fragment>
        }
        <StripePayment
          replacementIDString={'action="CALLBACK_URL" method="post"'}
          ref={(node) => { this.stripePayment = node; }}
          markup={markup}
          paymentCallback={this.handlePaymentCallback}
          zeroPaymentCallback={this.checkoutUsingCashback ? this.checkoutUsingCashback.openCheckoutUsingCashback : null}
        />
        <CheckoutUsingCashback
          ref={node => this.checkoutUsingCashback = node}
          onConfirm={this.handleOnCheckoutZero}
        />
        <CheckoutComplete
          className="blue-popup-backdrop"
          wrappedComponentRef={node => this.checkoutComplete = node}
        />
        <div ref={node => this.gaReceipt = node} />
      </Fragment>
    );
  }
}

CheckoutPayment.defaultProps = {
  wordings: [],
  trackingTags: null,
  charged: false,
  promoCode: null,
  useCashback: null,
  id: null,
};

CheckoutPayment.propTypes = {
  getStripe: PropTypes.func.isRequired,
  sessionId: PropTypes.string.isRequired,
  checkout: PropTypes.func.isRequired,
  wordings: PropTypes.array,
  trackingTags: PropTypes.object,
  setError: PropTypes.func.isRequired,
  charged: PropTypes.bool,
  transactionData: PropTypes.object.isRequired,
  values: PropTypes.object.isRequired,
  useCashback: PropTypes.bool,
  promoCode: PropTypes.string,
  category: PropTypes.string.isRequired,
  getPromo: PropTypes.func.isRequired,
  id: PropTypes.number,
  setErrors: PropTypes.func.isRequired,
};

export default CheckoutPayment;
