/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import CashbackBalance from 'components/Templates/CashbackBalance/CashbackBalanceNew';
import Checkbox from 'components/FormsV3/Checkbox/Checkbox';

import './CheckoutCashback.scss';

class CheckoutCashback extends Component {
  render() {
    const {
      isLoggedIn,
      rewardPoints,
      charged,
      transactionData,
      values,
      errors,
      handleOnChange,
      getPromo,
    } = this.props;

    if (
      (!isLoggedIn && !charged) ||
      !transactionData ||
      !transactionData.transaction ||
      !transactionData.transaction.price
    ) {
      return null;
    }

    const priceData = transactionData.transaction.price;
    const cashbackData = _.find(priceData, data => data.title === 'P$ Cashback');

    const cashbackUsed = cashbackData ? cashbackData.description : '$0';

    let cashbackBalance = '';
    try {
      cashbackBalance = cashbackData && !charged ?
        rewardPoints - _.replace(cashbackUsed, '-$', '') :
        rewardPoints;
    } catch (error) {
      cashbackBalance = rewardPoints;
    }

    // In case null
    cashbackBalance = cashbackBalance || 0;
    cashbackBalance = `${cashbackBalance.toFixed(2)}`;

    const chargedBalanceStyle = charged ? 'cashback-balance-charged' : '';

    return (
      <Fragment>
        <Col xs={12} className="summary-seperator" />
        <Col xs={12}>
          <Row className="cashback" noGutters>
            {
              isLoggedIn ?
                <Col xs={12} sm={7}>
                  <div className="cashback-inner">
                    {
                      charged ?
                        <span className="cashback-text">Cashback Balance</span>
                      :
                        <div className="cashback-checkbox">
                          <Checkbox
                            value={values.cashback && values.cashback.value}
                            name="cashback"
                            desc={<span>Use Cashback Balance</span>}
                            onChange={async (type, name, value) => {
                              await handleOnChange(type, name, value);
                              getPromo();
                            }}
                            errorString={errors.cashback}
                            isRequired
                          />
                        </div>
                    }
                  </div>
                  {
                    isLoggedIn ?
                      <div className={`cashback-balance ${chargedBalanceStyle}`}>
                        <div className="cashback-balance-display">
                          <CashbackBalance cashbackBalance={cashbackBalance} />
                        </div>
                      </div>
                    :
                      null
                  }
                </Col>
              : <Col xs={12} sm={7} />
            }
            <Col xs={4} sm={7} className="d-block d-sm-none" />
            <Col xs={8} sm={5}>
              <div className="summary-item-container">
                <span className="summary-item-desc">Cashback Used</span>
                <span className="summary-item-value">{cashbackUsed}</span>
              </div>
            </Col>
          </Row>
        </Col>
      </Fragment>
    );
  }
}

CheckoutCashback.defaultProps = {
  rewardPoints: null,
  charged: false,
};

CheckoutCashback.propTypes = {
  transactionData: PropTypes.object.isRequired,
  isLoggedIn: PropTypes.bool.isRequired,
  rewardPoints: PropTypes.number,
  values: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
  charged: PropTypes.bool,
  handleOnChange: PropTypes.func.isRequired,
  getPromo: PropTypes.func.isRequired,
};

export default CheckoutCashback;
