/**
 * Checkout.js
 * This is the checkout view that will render the summary
 * of details and handle all the promo, cashback and stripe
 * APIs.
 *
 * There is 2 state to this view. Checkout or receipt. It will
 * toggle accordingly if charged/ahHocCharged.
 *
 * Upon successful payment, it will update the user data again
 * to check if any cashback is used.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import { Col } from 'reactstrap';

import Checkbox from 'components/FormsV3/Checkbox/Checkbox';

import './CheckoutDisclaimer.scss';

class CheckoutDisclaimer extends Component {
  render() {
    const {
      wordings,
      charged,
      values,
      errors,
      handleOnChange,
    } = this.props;

    if (charged) {
      return null;
    }

    return (
      <Fragment>
        {
          _.map(wordings, (wording, index) => {
            const name = `tnc_${index}`;
            return (
              <Col key={name} xs={12}>
                <div className="disclaimer">
                  <Checkbox
                    key={name}
                    value={values[name] && values[name].value}
                    name={name}
                    desc={wording}
                    onChange={handleOnChange}
                    errorString={errors[name]}
                    isRequired
                  />
                </div>
              </Col>
            );
          })
        }
      </Fragment>
    );
  }
}

CheckoutDisclaimer.defaultProps = {
  wordings: [],
  charged: false,
};

CheckoutDisclaimer.propTypes = {
  values: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired,
  wordings: PropTypes.array,
  charged: PropTypes.bool,
  handleOnChange: PropTypes.func.isRequired,
};

export default CheckoutDisclaimer;
